@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&family=Montserrat:wght@300;400;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

.main{padding:20px 0;background-color:#fa6464;min-height:calc(100vh - 222px)}

.homepage__wrap{display:flex;flex-direction:column;align-items:center}@media (min-width: 768px){.homepage__wrap{flex-direction:row}}.homepage__title{font-size:50px;font-family:'Josefin Sans', sans-serif;margin-bottom:40px}.homepage__description{font-family:'Montserrat', sans-serif;font-size:20px;margin-bottom:20px}.homepage__slider{z-index:5}.homepage__link{display:block;padding:20px;height:20px;width:77px;margin:0 auto;text-decoration:none;border-radius:10%;border:2px solid;font-family:'Montserrat', sans-serif;color:#000;font-size:20px;transition:all 0.3s}.homepage__link:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}

.slider-component{display:block;width:300px;z-index:10}@media (min-width: 768px){.slider-component{width:450px}}@media (min-width: 1024px){.slider-component{width:600px}}.slider-component__img{display:block;width:90%;height:90%;object-fit:cover;z-index:100;transition:all 0.3s}.slider-component__img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}

.winelist{height:200px}


.header{position:-webkit-sticky;position:sticky;top:0;display:flex;align-items:center;justify-content:space-between;width:100%;height:94px;border-bottom:1px solid black;overflow:hidden;z-index:10;background-color:#fa6464}.header__logo{margin-left:34px}.nav__list{height:100%;font-family:'Montserrat', sans-serif;font-size:20px;max-width:1200px;display:flex;flex:row 1;text-decoration:none;list-style:none;grid-gap:50px;gap:50px;position:relative}.nav__link{display:block;margin:auto;box-sizing:border-box;text-align:center;align-items:center;text-decoration:none;color:black;padding:41px 34px;text-transform:uppercase;font-size:12px;font-weight:800;letter-spacing:0.04em;position:relative;border-bottom:3px solid #00000000}.nav__link--active{color:black;border-color:#000}

.breadcrumbs{padding:0 32px}.breadcrumbs__icon{height:32px;width:32px}

.footer{display:flex;justify-content:space-between;align-items:center;background-color:#f0f0f0;padding:20px;background-color:#fa6464;border-top:1px solid black;position:relative;bottom:0;font-family:'Montserrat', sans-serif;font-size:15px}.footer__logo,.footer__button{display:flex;justify-content:center;align-items:center;cursor:pointer;padding:10px;border:1px solid #ccc;border-radius:5px}.footer__links{display:flex;justify-content:space-between;grid-gap:36px;gap:36px}.footer__links-link{text-decoration:none;color:#000;cursor:pointer}

.container-wrap{padding:0 20px;max-width:1020px;margin:0 auto}

